import React from "react";
import ReactDOM from "react-dom";
import bridge from "@vkontakte/vk-bridge";
import {
  AdaptivityProvider,
  ConfigProvider,
  useAdaptivity,
  AppRoot,
  SplitLayout,
  SplitCol,
  ViewWidth,
  View,
  Panel,
  PanelHeader,
  Header,
  Group,
  SimpleCell,
  FixedLayout,
  PromoBanner
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";

const promoBannerProps = {
  title: 'Заголовок',
  domain: 'vk.com',
  trackingLink: 'https://vk.com',
  ctaText: 'Перейти',
  advertisingLabel: 'Реклама',
  iconLink: 'https://sun9-7.userapi.com/c846420/v846420985/1526c3/ISX7VF8NjZk.jpg',
  description: 'Описание рекламы',
  ageRestrictions: "14+",
  statistics: [
    { url: '', type: 'playbackStarted' },
    { url: '', type: 'click' }
  ]
};

 
const App = () => {
  const { viewWidth } = useAdaptivity();
 
  return (
    <AppRoot>
      <SplitLayout header={<PanelHeader separator={false} />}>
        <SplitCol spaced={viewWidth && viewWidth > ViewWidth.MOBILE}>
          <View activePanel="promo">
  <Panel id="promo">
    <FixedLayout vertical="top">
      <PromoBanner bannerData={promoBannerProps} />
    </FixedLayout>
  </Panel>
</View>
        </SplitCol>
      </SplitLayout>
    </AppRoot>
  );
};
 
 bridge.send("VKWebAppInit", {});

bridge.send("VKWebAppGetAds",{})
 .then(data => {
        this.setState({ promoBannerProps });
    })
{ this.state.promoBannerProps && <PromoBanner bannerData={ this.state.promoBannerProps } /> }
 
ReactDOM.render(
  <ConfigProvider>
    <AdaptivityProvider>
      <App />
    </AdaptivityProvider>
  </ConfigProvider>,
  document.getElementById("root")
);



				